import React, { useState } from 'react'
import HomePage from './screens/HomePage'
import Header from './components/Header'
import Modal from 'react-modal'
import "./styles/App.css"
import ModalCustom from './components/Modal/ModalCustom'

const App: React.FunctionComponent = () => {
    const [isModal, setIsModal] = useState<boolean>(false)

  
    return (
        <div className="App">
            <Header isModal={isModal} setIsModal={setIsModal}/>

            <Modal
                isOpen={isModal}
                className="modal"
                ariaHideApp={false}
                shouldCloseOnEsc={true}
                shouldCloseOnOverlayClick={true}
            >
                <ModalCustom isModal={isModal} setIsModal={setIsModal} />
                {/* <div className="modal-container">
                    <h1 className="modal-title">Connect Wallet</h1>
                    
                    <div className="modal-main">
                        {   error ? (
                            <>
                            <p className="modal-main-text">
                                <GoAlert color="red" fontSize="2rem"/>
                            </p>
                            <p className="modal-main-text">
                                Something went wrong, try later!
                            </p>
                            </>
                        ) 
                            : active ? 
                                <p className="modal-main-text">{account}</p> : 
                                <p className="modal-main-text">Would u want to connect? Just click connect</p>
                            }
                    </div>

                    <div className="modal-btn">
                        <button
                            className="btn-connect"
                            onClick={connect}
                        >Connect</button>
                        <button
                            className="btn-disconnect"
                            onClick={disconnect}
                        >Disconnect</button>
                    </div>
                    
                </div> */}
            </Modal>
            <HomePage />
        </div>
    )
}

export default App