import React, { useState, useEffect } from 'react'
import { GoAlert } from "react-icons/go";

declare global {
    interface Window {
        tronWeb:any;
    }
}

interface ModalCustomProps {
  isModal: boolean
  setIsModal: React.Dispatch<React.SetStateAction<boolean>>
}
// interface IDetail {
//     name: string
//     address: string
//     balance: number
//     frozenBalance: number
//     network: string
//     link: boolean
// }
const ModalCustom: React.FunctionComponent<ModalCustomProps> = ({ isModal, setIsModal }) => {
    const [walletDetail, setWalletDetail] = useState({
        name: 'none',
        address: 'none',
        balance: 0,
        frozenBalance: 0,
        network: 'none',
        link: 'false'
    })
    const [result, setResult] = useState<number>(2) 

    const getBalance = async () => {
        if(window.tronWeb && window.tronWeb.ready) {
            let walletBalances = await window.tronWeb.trx.getAccount(
                window.tronWeb.defaultAdreess.base58
            )
            return walletBalances
        } else {
            return 0
        }
    }

    const getWalletDetail = async () => {
        if(window.tronWeb) {
            if(window.tronWeb.ready) {
                let tempBalance = await getBalance();
                let tempFrozenBalance = 0

                if(!tempBalance.balance) {
                    tempBalance.balance = 0
                }

                if (
                    !tempBalance.frozen &&
                    !tempBalance.account_resource.frozen_balance_for_energy
                  ) {
                    tempFrozenBalance = 0;
                  } else {
                    if (
                      tempBalance.frozen &&
                      tempBalance.account_resource.frozen_balance_for_energy
                    ) {
                      tempFrozenBalance =
                        tempBalance.frozen[0].frozen_balance +
                        tempBalance.account_resource.frozen_balance_for_energy
                          .frozen_balance;
                    }
                    if (
                      tempBalance.frozen &&
                      !tempBalance.account_resource.frozen_balance_for_energy
                    ) {
                      tempFrozenBalance = tempBalance.frozen[0].frozen_balance;
                    }
                    if (
                      !tempBalance.frozen &&
                      tempBalance.account_resource.frozen_balance_for_energy
                    ) {
                      tempFrozenBalance =
                        tempBalance.account_resource.frozen_balance_for_energy
                          .frozen_balance;
                    }
                  }
                  setResult(0)
                  setWalletDetail({
                    name: window.tronWeb.defaultAddress.name,
                    address: window.tronWeb.defaultAddress.base58,
                    balance: tempBalance.balance / 1000000,
                    frozenBalance: tempFrozenBalance / 1000000,
                    network: window.tronWeb.fullNode.host,
                    link: 'true',
                  })
            } else {
                console.log('Wallet detected please login')
                setResult(1)
            }
        } else {
            setResult(2)
            console.log('Wallet was not detected')
        }
    }
    useEffect(() => {
        const interval = setInterval(async () => {
            getWalletDetail();
          //wallet checking interval 2sec
        }, 20000);
        return () => {
          clearInterval(interval);
        };
      });

    const renderContent = () => {
        if(result === 0) {
            return (
                <>
                  <h1 className="modal-title">Wallet connected</h1> 
                   <div className="modal-main">
                       <div className="modal-main-text">
                       <h4>Account Name: {walletDetail.name} </h4>
                        <h4>My Address: {walletDetail.address}</h4>
                        <h4>
                          Balance: {walletDetail.balance} TRX (Frozen:{' '}
                          {walletDetail.frozenBalance} TRX)
                        </h4>
                        <h4>Network Selected: {walletDetail.network}</h4>
                        <h4>Link Established: {walletDetail.link}</h4>
                       </div>
                   </div>
                   <div className="modal-btn">
                       <button
                            className="btn-disconnect"
                            onClick={() => setIsModal(!isModal)}
                       >Close</button>
                   </div>
                </>
            )
        } else if (result === 1) {
            return (<>
                <h1 className="modal-title">Wallet detected, please login</h1>
                <div className="modal-btn">
                       <button
                            className="btn-disconnect"
                            onClick={() => setIsModal(!isModal)}
                       >Close</button>
                   </div>
            </>)
        } else if (result === 2) {
            return (
                <>
                    <h1 className="modal-title">
                        <GoAlert color="red" fontSize="2rem"/>
                        Wallet was not found
                    </h1>
                </>
            )
        }
    }
    return (
        <div className="modal-container">
            {renderContent()}
        </div>
    )
}

export default ModalCustom